<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h1 class="blue--text text--darken-2 mb-3">Orders</h1>
        <v-list
            subheader
            two-line
            v-for="order in orders"
            :key="order.id"
        >
          <v-list-tile>
            <v-list-tile-action>
              <v-checkbox :input-value="order.done"
                          @change="markDone(order)"
                          color="success"
              ></v-checkbox>
            </v-list-tile-action>

            <v-list-tile-content @click="order.done = !order.done" class="pointer">
              <v-list-tile-title>{{order.name}}</v-list-tile-title>
              <v-list-tile-sub-title>{{order.phone}}</v-list-tile-sub-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn class="primary" :to="{name: 'ad', params: {id: order.adId}}">Open</v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'Orders',
  data () {
    return {
      orders: [
        {
          id: 'ord1',
          name: 'Diana',
          phone: '8-123-456',
          adId: '1',
          done: false
        },
        {
          id: 'ord2',
          name: 'Vladimir',
          phone: '8-345-456',
          adId: '2',
          done: false
        },
        {
          id: 'ord3',
          name: 'Giorgi',
          phone: '8-567-456',
          adId: '3',
          done: false
        }
      ]
    }
  },
  methods: {
    markDone (order) {
      order.done = true
    }
  }

}
</script>

<style scoped>
.pointer{
  cursor: pointer;
}
</style>



