<template>
  <v-container>
    <v-layout row>
      <v-flex xs12>
<!--        {{ad}}-->
        <v-card>
          <v-img
              :src="ad.imageSrc"
              aspect-ratio="1.7778"
          >
          </v-img>
          <v-card-text>
            <h1>{{ad.title}}</h1>
            <p>{{ad.description}}</p>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="warning" flat>Edit</v-btn>
            <v-btn color="success">Buy</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'Ad',
  props: ['id'],
  computed: {
    ...mapGetters('ads', {
      ads: 'ads',
      getAd: 'ad',
      getAdById: 'adById'
    }),
    ad () {
      // console.log(this.id)
      return this.getAd(this.id)
      // return this.getAdById(this.id)
    }
  }
}
</script>

<style scoped>
</style>



