<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h1 class="blue--text text--darken-2 mb-3">Ad List</h1>
        <v-card class="elevation-10 mb-3"
                v-for="ad in myAds"
                :key="ad.id"
        >
          <v-layout row>
            <v-flex xs4>
              <v-img
                  :src="ad.imageSrc"
                  aspect-ratio="1.7778"
              >
              </v-img>
            </v-flex>
            <v-flex xs8>
              <v-card-text>
                <h2 class="text--primary">{{ad.title}}</h2>
                {{ad.description}}
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn class="primary" :to="{name: 'ad', params: {id: ad.id}}">Open</v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'AdList',
  data () {
    return {}
  },
  computed: {
    ...mapGetters('ads', {
      ads: 'ads',
      myAds: 'myAds'
    })
  }
}
</script>

<style scoped>
  .v-image {
    max-width: 100%;
    min-height: 100%;
  }
</style>



