export default {
  namespaced: true,
  state: {
    ads: [
      {
        id: '1',
        title: 'First ad',
        description: 'Hello i am description',
        promo: true,
        imageSrc: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg'
      },
      {
        id: '2',
        title: 'Second ad',
        description: 'Hello i am description',
        promo: true,
        imageSrc: 'https://cdn.vuetifyjs.com/images/carousel/sky.jpg'
      },
      {
        id: '3',
        title: 'Third ad',
        description: 'Hello i am description',
        promo: false,
        imageSrc: 'https://cdn.vuetifyjs.com/images/carousel/bird.jpg'
      },
      {
        id: '4',
        title: 'Fourth ad',
        description: 'Hello i am description',
        promo: true,
        imageSrc: 'https://cdn.vuetifyjs.com/images/carousel/planet.jpg'
      },
      {
        id: '5',
        title: 'Fifth ad',
        description: 'Hello i am description',
        promo: true,
        imageSrc: 'https://cdn.vuetifyjs.com/images/carousel/bird.jpg'
      },
      {
        id: '6',
        title: 'Sixth ad',
        description: 'Hello i am description',
        promo: false,
        imageSrc: 'https://cdn.vuetifyjs.com/images/carousel/planet.jpg'
      }
    ]
  },
  getters: {
    ads (state, getters) {
      return state.ads.sort((a, b) => {
        return a.id > b.id ? -1 : 1
      })
    },
    // adsMap (state, getters) {
    //   let map = {}
    //   for (let i = 0; i < state.ads.length; i++) {
    //     let ad = state.ads[i]
    //     map[ad.id] = ad
    //   }
    //   return map
    // },
    adsMap (state, getters) {
      let map = {}
      state.ads.forEach((item, i) => {
        map[item.id] = item
      })
      return map
    },
    ad: (state, getters) => (id) => {
      // console.log( id);
      // console.log( getters.products[id] );
      return getters.adsMap[id]
    },
    adById: (state, getters) => (id) => {
      return state.ads.find(ad => ad.id === id)
    },
    promoAds (state, getters) {
      return state.ads.filter(ad => ad.promo)
    },
    myAds (state, getters) {
      return getters.ads
    }
  },
  mutations: {
    createAd (state, payload) {
      state.ads.push(payload)
    }
  },
  actions: {
    createAd (store, payload) {
      return new Promise((resolve, reject) => {
        store.commit('createAd', payload)
        resolve()
      })
    }
  }

}



